import React, { Component } from 'react';
import './home.css'
import { ExchangeRow } from '../../components/exchange-row/exchange-row'

export class Exchange extends Component {
    state = {
        data: undefined,
        resp_date: undefined
    };
    componentDidMount() {
        console.log('componentDidMount')
        fetch('https://api.exchangeratesapi.io/latest?base=RUB')
            .then(r => r.json())
            .then(d=> {
                this.setState({ data: d.rates, resp_date: d.date })
            })
    }

    shouldComponentUpdate() {
        console.log('shouldComponentUpdate')
        return true;
    }

    componentDidUpdate() {
        console.log('componentDidUpdate')
    }

    componentWillUnmount() {
        console.log('componentWillUnmount')
    }
    
    render() {
        return (
           
            <div class="rates_block">
                <div class="rates_main_text">Курс валют на</div>
                <div class="rates_main_text_date">{this.state.resp_date}</div>
            <div class="rates_data">
                <div class="main_rates">
                    {
                        
                            !this.state.data
                                ? <div>Ошибка в закгрузке данных</div>
                                : ["USD","EUR","GBP","JPY"].map(key =><ExchangeRow k={key} value={this.state.data[key]} img_link={key+'.png'}/>)
                        
                    }
                </div>
                <div class="addit_rates"> 
                    {
                        !this.state.data
                            ? <div>Ошибка в закгрузке данных</div>
                            : Object.keys(this.state.data).map(key =>!(["USD","EUR","GBP","JPY","RUB"].includes(key))?<ExchangeRow k={key} value={this.state.data[key]}  img_link='default.png'/>:undefined)
                    }
                </div>
            </div>
            </div>
            
        );
    }
}
