import React from 'react';
import './header.css'
import {
    Link
} from "react-router-dom";

export class Header extends React.Component{
    constructor(props){
        super(props);
        this.state={isLogged: false}
    }

    componentDidMount(){
       
    }

    _renderLoader() {
        if (!this.props.position.isLogged) {
            return (
            <div class="header_buttons">
            <Link to='/signin'><button class = "sign_button">Sign IN</button> </Link>
            <Link to='/signup'><button class = "sign_button">Sign UP</button> </Link>
            </div>
            )
        }
        return (
            
            <div class="header_buttons">
            <Link to='/convertor'><button class = "sign_button">Конвертор</button> </Link>
            </div>
        );
    }

    render (){
    return (
        <div class="header_items">
            <div class="logo">
            <Link to='/' ><img class = "header_logo" src="logo.png" alt="" srcset=""/></Link>
            <span class="logo_text">.Конвертор</span> 
            </div>
            {this._renderLoader()}
        </div>
    );
}}
