import React from 'react';
import './signin.css'
import Firebase from '../../components/firebase/firebase'
import {
  useHistory,
  useLocation
} from "react-router-dom";

const fire = new Firebase();

const INITIAL_STATE = {
    username: '',
    user_password: ''
  }; 



export class SignIn extends React.Component{
constructor(props){
    super(props);
    this.state = { ...INITIAL_STATE };
}

    render(){
        const {
            username,
            user_password
          } = this.state; 
          
          const isInvalid = user_password === '' || username === '';
        return(
        <div id = "signin_basis">
            <div class="login_title">Вход</div>
            <form class = "submit_form" onSubmit={this.onSubmit}>
            <input class="line" name="username" value={username} onChange={this.onChange} type="text" placeholder="Логин"/>
            <input class="line"  name="user_password" value={user_password} onChange={this.onChange} type="text" placeholder="Пароль" />
            <button class = "sub_button" type = "submit">Войти</button>          
        </form>
        </div>
        );
    }


    onChange = event => {
        this.setState({ [event.target.name]: event.target.value });
      };
  
      onSubmit = event => {
        event.preventDefault();
        const { username, user_password } = this.state;
          fire.doLogIn(username,user_password,this.props.LoggedIn);
          alert(6)
          
          this.props.GoHome();
          
            
      }   
}