import React, { Component } from 'react';
import './App.css';
import { Header } from './components/header/header'
import { Exchange } from './pages/home/home'
import { SignUp} from './pages/signup/signup'
import {SignIn} from './pages/signin/signin'
import {Convertor} from './pages/convertor/convertor'

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
  useHistory
} from "react-router-dom";





class App extends Component {
constructor(props){
  super(props);
  this.state={isLogged: false}
  
}

componentDidUpdate(){
 
}


LoggedIn = () =>{
  this.setState({isLogged: true})
}

GoHome = () =>{
  
 }


  render() {
    console.log(this.state.isLogged)
    return (
      <Router>
        <Header position={this.state}/>
        {
          this.state.isLogged
            ?
            <Redirect to="/" />
            :
            null
        }
        <Switch>
        <Route path="/signup">
        <SignUp LoggedIn={this.LoggedIn}/>
          </Route>
          <Route path="/signin">
          <SignIn LoggedIn={this.LoggedIn} GoHome = {this.GoHome}/>
          </Route>
          <Route path="/convertor" >
          {!this.state.isLogged ? <Redirect to="/" /> : <Convertor />}
          </Route>
          <Route path="/">
            <Exchange />
          </Route>
        </Switch>
      </Router>
    );
  }
}

export default App;
