import React from 'react'
import './signup.css'
import Firebase from '../../components/firebase/firebase'

const fire = new Firebase();

const INITIAL_STATE = {
  username: '',
  email: '',
  FIO: '',
  user_password: '',
  file: '',
  isLogged: false
};

export class SignUp extends React.Component{
    
  constructor(props) {
    super(props);
 
    this.state = { ...INITIAL_STATE };
  }
  
    render(){

      const {
        username,
        email,
        FIO,
        user_password
      } = this.state; 
      
      const isInvalid = user_password === '' || email === '' || username === '';
        return(
          
        <div id = "signup_basis">
            <div class="reg_title">Регистрация</div>
            <form class = "submit_form" onSubmit={this.onSubmit}>
            <input class="line" name="username" value={username} onChange={this.onChange} type="text" placeholder="Логин"/>
            <div class="Info">Строчные и прописные буквы русского и латинского алфавита</div>
            <input class="line" name="email" value={email} onChange={this.onChange} type="text" placeholder="Email"/>
            <div class="Info">Формат ввода:__@__.__</div>
            <input class="line" name="FIO" value={FIO} onChange={this.onChange} type="text" placeholder="ФИО" />
            <div class="Info">10 цифр</div>
            <input class="line" name="user_password" value={user_password} onChange={this.onChange} type="text" placeholder="Пароль" />
            <div class="Info">10 цифр</div>
            <input class="line" id="file_name" type="file" ></input>
            <button class = "sub_button" disabled={isInvalid} type = "submit">Зарегистрироваться</button>          
        </form>
        </div>
        );
    }

    onChange = event => {
      this.setState({ [event.target.name]: event.target.value });
    };

    onSubmit = event => {
      const { username, email, FIO, user_password } = this.state;
      const file = document.getElementById('file_name').files[0];
        fire.doSingUp(username, email, FIO, user_password, file, this.props.LoggedIn)
        
        alert(6)
        event.preventDefault();
    }   
  }