import React from 'react';
import './exchange-row.css'

export const ExchangeRow = ({ k, value, img_link}) => {
    return (
        <div class="row">
           <img src={img_link} alt="" srcset="" class="valut_icon"/> 1 RUB = {value.toFixed(3)} {k}
           <div>1 {k} = {(1/value).toFixed(2)} RUB</div>
        </div>
    );
}
