import React, { Component } from 'react';

const INITIAL_STATE = {
    left_select: 'RUB',
    right_select:'',
    left_input:'',
    right_input:'0'
  };


export class Convertor extends Component {
    state = {
        data: undefined,
        ...INITIAL_STATE
    };

    componentDidMount() {
        fetch('https://api.exchangeratesapi.io/latest?base=RUB')
            .then(r => r.json())
            .then(d=> {
                this.setState({ data: d.rates})
            })
    }

    onChangeLeftSelect = event =>{
    fetch('https://api.exchangeratesapi.io/latest?base=' + event.target.value)
            .then(r => r.json())
            .then(d => this.setState({ data: d.rates }));
            
}  
onChangeRightSelect = event =>{
    
}

    render() {
        const{left_select, right_select, left_input, right_input} = this.state;
        return (
            <div>
                
                <div class = "convertor_box">
                    <input type="text"/>
                    <select name="left_select" value={left_select} onChange={this.onChangeLeftSelect}>
                                {
                                    !this.state.data
                                        ? <div>Данные не загружены</div>
                                        : Object.keys(this.state.data).map(key=><option value = {key}>{key}</option>)
                                }
                            </select>
                            
                            <select name="right_select" value={right_select}>
                                {
                                    !this.state.data
                                        ? <div>Данные не загружены</div>
                                        : Object.keys(this.state.data).map(key=><option value = {key}>{key}</option>)
                                }
                            </select>
                            <input type="text" value={right_input} />


                </div>
            </div>
        );
    }
}
